.search-box {
    width: calc(100% - 150px);
    max-width: 400px;
    background: red;
    position: relative;
    overflow: hidden;
    border-radius: 8px;
}
.search-box .search {
    padding: 9px 9px 9px 35px;
    border-radius: 8px;
    font-size: 14px;
    border: none;
    width: calc(100% - 44px);
    background-image: url(data:image/png;base64,);
    background-position: 3px center;
    background-repeat: no-repeat;
    font-family: 'Quicksand', sans-serif;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.search-box .search:focus {
    background-position: -30px center;
    padding-left: 5px;
    width: calc(100% - 14px);
    color:#000
}

.search-box .search-button {
    position: absolute;
    right: -35px;
    padding: 0 15px;
    font-size: 15px;
    line-height: 36px;
    border:none;
    background: #ffd700;
    color:#bf0a0a;
    -webkit-transition: .2s;
    transition: .2s;
}

.search-box .search:focus + .search-button {
    right: 0;
}
