.menu {
    width: 100%;
    position: fixed;
    z-index:4;
    left:0;
    top: 0px;
    -webkit-transition: 'background' 0.2s 0.2s;
    transition: 'background' 0.2s 0.2s;
    height: 100%;
    visibility: visible;
    background: rgba(0,0,0,.8);
}

.menu .content {
    height: 100%;
    background: #000;
    width: 90%;
    position: relative;
    left:0;
    padding: 15px 0;
    -webkit-transition: 0.4s .2s;
    transition: 0.4s .2s;
}

.menu .content .logo {
    width: 70%;
    margin: 0 15% 20px;
}

.menu .content ul {
    margin-top: 20px;
}

.menu .content ul li {
    color: #FFF;
    padding: 20px 30px;
    cursor: pointer;
    border-top: 1px solid #333;
    border-bottom: 1px solid #202020;
    font-family: 'Lato', sans-serif;
    text-transform: uppercase;
    position: relative;
    color: #ccc;
}

.menu .content ul li:hover {
    color:#ffd700
}

.menu .content ul li:first-child {
    border-top: none;
}

.menu .content ul li:last-child {
    border-bottom: none;
}

.menu .content ul li span {
    font-size: 45px;
    top: auto !important;
    right: auto !important;
    margin-left: -45px;
    background-color: #bf0a0a;
}

.menu .content ul li svg {
    margin-right: 15px;
    color:#ffd700
}

.menu.closed {
    background: rgba(0,0,0,0);
    visibility: hidden;
    -webkit-transition: 'background' 0.2s;
    transition: 'background' 0.2s;
}

.menu.closed .content {
    left: -500px;
    -webkit-transition: 0.4s 0s;
    transition: 0.4s 0s;
}

.menu .search-box {
    width: 90%;
    margin:0 5%
}

@media (min-width: 1000px) { 
    .menu.closed {
        background: #000;
        visibility: visible;
    }
    .menu.closed .content {
        left: 0;
    }
    .menu .logo , .menu .search-box {
        display: none;
    }

    .menu {
        width: 100%;
        z-index:4;
        left:0;
        top: 73px;
        height: auto;
    }
    
    .menu .content {
        width: 100%;
        padding: 15px 0;
        -webkit-transition: 0.4s .2s;
        transition: 0.4s .2s;
    }

    .menu .content ul {
        display: flex;
        margin-top: 0px;
        justify-content: space-around;
    }

    .menu .content ul li {
        text-align: center;
        border: none;
        padding: 0;
    }

    .menu .content ul li:first-child {
        display: none;
    }

}
/* Slider */
.slick-slider
{
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}

